<template>
  <div id="Mine">
    <div id="title">
      <mt-header title="我的">
      </mt-header>
    </div>
    <!--头像-->
    <div class="img">
       <img style="width: 2rem;height:2rem;border-radius: 2rem;margin-top: 1rem;border: 2px solid white" :src="customerImg"/>
